header {
    height: 100vh;
    padding-top: 7rem;
    /* overflow: hidden; */
}

.header_container {
    text-align: center;
    height: 100%;
    position: relative;
}

/* ======== CTA ========*/

.cta {
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

/* ======== Socials ========*/

.header_socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header_socials::after{
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

/* ======== IMG ========*/

.me{
    /* background: var(--color-primary); */
    width: 40rem;
    height: 5rem;
    position: absolute;
    left: calc(50% - 20rem);
    margin-top: 10rem;
}

/* ======== Scrol down ========*/

.scroll_down {
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
}

/*------- Media queries (Medium Devices) ---------- */

@media screen and (max-width: 1024px) {
    header{
        height: 78vh;
    }
    .me{
        width: 30rem;
        position: absolute;
        left: calc(50% - 15rem);
        margin-top: 3rem;
    }
}
/*------- Media queries (Small Devices) ---------- */

@media screen and (max-width: 600px) {
        header{
            height: 68vh;
        }
        .header_socials, .scroll_down{
            display: none;
        }

        .me {
            width: 20rem;
            position: absolute;
            left: calc(50% - 10rem);
            margin-top: 3rem;
            }
}

@media screen and (max-width: 600px) {
        header {
                height: 68vh;
        }
         .me {
            width: 14rem;
            position: absolute;
            left: calc(50% - 7rem);
            margin-top: 3rem;
        }
}
